import React, {useEffect, useState} from "react";
import { Link, graphql } from "gatsby";
import rehypeReact from "rehype-react";
import ReactGA from 'react-ga';

import SEO from "../../components/SEO";
import Layout from "../../components/layout";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Youtube from './components/Youtube';
import ImageComparison from './components/ImageComparison';

import "./styles.scss";

function AnalyticsLink(props) {
  return (<ReactGA.OutboundLink
    eventLabel="OutboundLink"
    to={props.href}
    trackerNames={[]}
  >
    {props.children}
  </ReactGA.OutboundLink>);
}

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: {
    "img": LazyLoadImage,
    "youtube": Youtube,
    "img-compare": ImageComparison,
    "a": AnalyticsLink,
  },
}).Compiler;

export default function Template({
  data
}) {
  const [start] = useState(new Date());
  const { markdownRemark,} = data;
  const { excerpt, frontmatter, htmlAst, id } = markdownRemark;

  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.timing({
        category: 'TimeOnBlog',
        variable: 'time',
        value: diff, // in milliseconds
        label: id,
      });
    };
  });

  return (
    <Layout>
      <SEO
        title={frontmatter.title}
        img={frontmatter.cover_photo}
        description={excerpt}
        keywords={frontmatter.tags}
        urlPath={`${id}`}
      />
      <section className="blog-post-container">
        <div className="blog-post">
          <h1 className="Title">{frontmatter.title}</h1>
          <div className="Date">Published: {frontmatter.date}</div>
          <LazyLoadImage src={frontmatter.cover_photo} alt={frontmatter.title} />

          <div className="blog-post-content">
            {renderAst(htmlAst)}
          </div>

          <div className="blog-post-tags">
            {frontmatter.tags.map((tag) => {
              return (<Link key={tag} className="blogTag" to={`/${tag.toLowerCase()}`}>#{tag}</Link>);
            })}
          </div>
        </div>
      </section>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      htmlAst
      id
      excerpt
      frontmatter {
        date(formatString: "MMMM D, YYYY")
        slug
        title
        tags
        cover_photo
      }
    }
  }
`